@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap);
.container {
  position: relative;
  width: 100%;
  max-width: 1920px;
  display: block;
  margin: 0 auto;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 80px;
  padding: 0 120px 0 120px;
  margin-bottom: 60px;
  background: #F7F7FC;
  box-shadow: 0 8px 16px 0 rgba(17, 17, 17, 0.04);
}

.avatar-block {
  display: flex;
  align-items: center;
}

.avatar {
  width: 48px;
  height: 48px;
  display: block;
  border: 1.5px solid #00BA88;
  border-radius: 100px;
  margin-right: 25px;
  background-repeat: no-repeat;
  background-position: center;
}

.buttons-wrapper {
  display: flex;
}

.nav-menu {
  display: flex;
}

.nav-menu .nav-button {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 20px 0 20px;
  cursor: pointer;
}

.nav-menu .nav-button img {
  margin-left: 11px;
}

.log-out {
  margin-left: 80px;
  cursor: pointer;
}

.content-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 0 120px 0 120px;
}

.page-title {
  color: #14142B;
  font-size: 36px;
  margin-bottom: 60px;
}

.dashboard-info {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.dashboard-info__block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid #D6D8E7;
  box-sizing: border-box;
  border-radius: 32px;
  background: #FFFFFF;
  padding: 80px 75px;
  margin-right: 80px;
}

.block-title {
  font-size: 36px;
  line-height: 34px;
  padding: 0;
  margin: 0 0 50px 0;
}

.block-count {
  font-weight: 600;
  font-size: 48px;
  line-height: 34px;
  color: rgba(20, 20, 43, 1);
  padding: 0;
  margin: 0;
}

.users-info {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.users-info__block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid #D6D8E7;
  border-radius: 16px;
  background: #FFFFFF;
  padding: 39px 50px;
  margin-right: 32px;
  margin-bottom: 60px;
  width: 396px;
  height: 208px;
}

.users-info__block:nth-child(4n) {
  margin-right: 0;
}

.user-title {
  font-weight: 500;
  font-size: 24px;
  line-height: 36px;
  letter-spacing: 0.75px;
  color: #4E4B66;
  padding: 0;
  margin: 0;
}

.user-email {
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 36px;
  letter-spacing: 0.75px;
  color: #4E4B66;
  padding: 20px 0 20px 0;
  margin: 0;
}

.user-profiles {
  padding: 0;
  margin: 0;
}


.profiles-info {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.profiles-info__block {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border: 1px solid #D6D8E7;
  border-radius: 16px;
  background: #FFFFFF;
  padding-top: 35px;
  margin-right: 32px;
  margin-bottom: 60px;
  width: 320px;
  height: 298px;
  position: relative;
}

.profiles-info__block p {
  margin: 0;
}

.profile-title {
  padding-top: 0;
}

.p-t {
  padding-top: 15px;
}

.card-buttons {
  position: absolute;
  display: flex;
  width: 100%;
  bottom: 0;
  border-top: 1px solid #D6D8E7;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 40px;
  background-color: transparent;
  border: none;
}

.diving-line {
  width: 0;
  border-right: 1px solid #D6D8E7;
}

.form-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
}

.form-title {
  margin-top: 120px;
  margin-bottom: 130px;
  font-weight: 600;
  font-size: 48px;
  line-height: 72px;
  letter-spacing: 1px;
  color: #14142B;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form label {
  display: flex;
  flex-direction: column;
  font-weight: normal;
  font-size: 18px;
  line-height: 34px;
  letter-spacing: 1px;
  color: #6E7191;
  margin-bottom: 25px;
}

.form input {
  border: none;
  background-color: transparent;
  margin: 0;
  padding: 0;
  width: 400px;
  border-bottom: 1px solid #14142B;;
  font-weight: normal;
  font-size: 24px;
  line-height: 34px;
  letter-spacing: 1px;
  color: #14142B;
}

.form-submit-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 25px;
  background: #EFF0F6;
  border-radius: 12px;
  border: none;
  margin-top: 90px;
  font-weight: normal;
  font-size: 24px;
  letter-spacing: 0.75px;
  color: #4E4B66;
  cursor: pointer;
}

.form .isAdmin {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.form .isAdmin input[type="checkbox"] {
  width: 6px;
  height: 6px;
  margin-right: 12px;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-style: normal;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

#root {
  height: 100vh;
  background: #F7F7FC;
}

